import NProgress from 'nprogress'  //引入页面加载顶部进度条库
import 'nprogress/nprogress.css'  //引入该进度条的样式

// 全局进度条的配置
NProgress.configure({
    easing: 'ease',  // 动画方式    
    speed: 500,  // 递增进度条的速度
    showSpinner: true, // 是否显示加载icon
    trickleSpeed: 800, // 自动递增间隔    
    trickRate: 0.02,
    minimum: 0.1 // 初始化时的最小百分比
})

// 打开进度条
export const start = () => {
    NProgress.start()
}

// 关闭进度条
export const close = () => {
    NProgress.done()
}