//main.js 程序入口文件，初始化vue实例，并引入使用需要的插件和各种公共组件
//import...from...是ES6语法里面的新特性，用来引入外部文件
import Vue from 'vue'  //引入vue组件库
import App from '@/App'  //引入一个当前目录下的名字为App.vue的组件
import router from '@/router'  //引入路由组件
import store from '@/store'
import '@/plugins'  //  当文件夹里有index.js的文件,@/plugins即为@/plugins/index.js的简写，其他命名方式则不行
import 'normalize.css'  //引入初始化css文件，重置浏览器样式（main.js引入即为全局使用）
import { start, close } from '@/utils/nprogress.js'

Vue.config.productionTip = false  //是否要在生产环境当中给予提示功能（ 不设置的话默认为true开启 ），布尔值设为false即为关闭，为true即为打开

router.beforeEach(async (to, from, next) => {
  // 开启加载进度条  
  start()
  next()
})

router.afterEach(() => {
  // 关闭加载进度条
  close()
})

new Vue({  //生成一个Vue实例
  router,  //注册路由，把router 实例注入到 vue 根实例中
  store,  //注册vuex仓库
  render: h => h(App),  // 渲染函数,将组件App渲染到挂载的元素上（ =>是ES6新增的箭头函数 ）
}).$mount('#app')  //这个#app是挂载在该项目public文件夹的index.html文件的id，你打开index.html就会看到这个元素id是app（另一种写法--在花括号里用el:'#app'替换）