import Vue from 'vue';   //引入Vue组件库
import VueRouter from 'vue-router'
import index from '@/views/index'
import { start, close } from '@/utils/nprogress.js'

Vue.use(VueRouter)

const routes = [
    { path: '/', component: index }
]

const router = new VueRouter({
    routes
})

export default router